(() => {
  const inputSearch = document.querySelector('.search__form .form__input');

  inputSearch.addEventListener('focus', (event) => {
    event.target.parentElement.classList.add('form__label--focused');
  })

  inputSearch.addEventListener('blur', (event) => {
    event.target.parentElement.classList.remove('form__label--focused');
  })

  inputSearch.addEventListener('mousedown', (event) => {
    event.target.parentElement.classList.add('form__label--active');
  })

  inputSearch.addEventListener('mouseup', (event) => {
    event.target.parentElement.classList.remove('form__label--active');
  })

})()
