(() => {
  const tooltipList = document.querySelectorAll('.tooltip');

  tooltipList.forEach((tooltip) => {
    tooltip.addEventListener('mouseenter', () => {
      displayTooltip(tooltip)
    })

    tooltip.addEventListener('mouseleave', () => {
      hideTooltip(tooltip)
    })
  })

  function displayTooltip(tooltip) {
    const textPanel = tooltip.querySelector('.tooltip__panel');

    textPanel.classList.add('tooltip__panel--displayed')
  }

  function hideTooltip(tooltip) {
    const textPanel = tooltip.querySelector('.tooltip__panel');
    textPanel.classList.remove('tooltip__panel--displayed')
  }
})()
