(() => {
  const body = document.querySelector('body');
  const burgerBtn = document.getElementById('burger-btn');
  const navBtnClose = document.querySelector('.nav__btn-close');
  const navModal = document.querySelector('.nav__modal');
  const navList = navModal.querySelector('.nav__list')

  burgerBtn.addEventListener('click', () => {
    openNavigation();
  })

  navBtnClose.addEventListener('click', () => {
    closeNavigation();
  })

  function openNavigation() {

    navModal.classList.add('nav__modal--open');
    body.classList.add('stop-scroll');

    setTimeout(() => {
      navModal.classList.add('nav__modal--non-opacity');
      navList.classList.add('nav__list--open');
    }, 1);
  }

  function closeNavigation() {

    navModal.classList.remove('nav__modal--non-opacity');
    navList.classList.remove('nav__list--open');

    setTimeout(() => {
      body.classList.remove('stop-scroll');
      navModal.classList.remove('nav__modal--open');
    }, 550);
  }

})()
