(() => {
  const checkboxList = document.querySelectorAll('.js-custom-checkbox');

  checkboxList.forEach(checkbox => {
    const input = checkbox.querySelector('.form__input');

    input.addEventListener('click', (event) => {
      toggleCheckboxChecked(event, checkbox)
    })
  })

  function toggleCheckboxChecked(event, checkbox) {

    if (event.target.checked) {
      checkbox.classList.add('form__custom-checkbox--checked');
      checkbox.setAttribute('aria-checked', true)
    } else {
      checkbox.classList.remove('form__custom-checkbox--checked');
      checkbox.setAttribute('aria-checked', false)
    }
  }


})()
