const swiperItems = document.querySelectorAll('.swiper');

swiperItems.forEach((item) => {

  const swiperId = item.dataset.swiperId;
  let swiper;

  if (swiperId === 'bans') {
    swiper = new Swiper(item, {

      direction: 'horizontal',
      loop: true,
      grabCursor: true,

      pagination: {
        el: '.swiper-pagination',
      },
    })
  } // bans

  if (swiperId === 'specials') {

    swiper = new Swiper(item, {

      direction: 'horizontal',
      loop: true,
      slidesPerView: 3,
      slidesPerGroup: 3,
      grabCursor: true,
      spaceBetween: 32,

      breakpoints: {
        320: {
          slidesPerView: 1,
          slidesPerGroup: 1,
        },

        768: {
          slidesPerView: 2,
          slidesPerGroup: 1,
        },

        1024: {
          slidesPerView: 3,
          slidesPerGroup: 1,
        },

        1280: {
          slidesPerView: 3,
          slidesPerGroup: 3,
        },
      },
    })

    setControlButtonsSlide(swiperId, item);
  } // specials

  if (swiperId === 'usefuls') {
    swiper = new Swiper('.usefuls__swiper', {
      direction: 'horizontal',
      loop: true,
      grabCursor: true,
      slidesPerView: 2,
      spaceBetween: 32,

      breakpoints: {

        320: {
          slidesPerView: 1,
         },

        768: {
          slidesPerView: 2,
         },

        1024: {
          slidesPerView: 3,
         },

        1280: {
          slidesPerView: 2,
        },
      },
    })

    setControlButtonsSlide(swiperId, item);
  } // usefuls

  if (swiperId === 'catalog') {

    swiper = new Swiper(item, {
      direction: 'horizontal',
      loop: false,
      slidesPerView: 1,
      grid: {
        fill: 'row',
        rows: 3,
      },
      grabCursor: true,
      spaceBetween: 32,

      breakpoints: {
        320: {
          slidesPerView: 2,
          slidesPerGroup: 2,
          spaceBetween: 16,
        },
        1024: {
          slidesPerView: 3,
          slidesPerGroup: 3,
        },
      },
    })
    setControlButtonsJump(swiperId, item);
  } // catalog

  if (swiperId === 'related-product') {
    swiper = new Swiper(item, {
      direction: 'horizontal',
      loop: true,
      grid: {
        fill: 'row',
        rows: 1,
      },
      grabCursor: true,
      spaceBetween: 38,

      breakpoints: {
        320: {
          slidesPerView: 2,
          spaceBetween: 16,
        },

        767: {
          slidesPerView: 2,
        },

        1024: {
          slidesPerView: 3,
        },

        1309: {
          slidesPerView: 4,
        },
      },
    })

    setControlButtonsSlide(swiperId, item);
  } // related-product

  if (swiperId === 'thumbs-main') {
    swiper = new Swiper(item, {
      direction: 'horizontal',
      slidesPerView: 3,
      slidesPerGroup: 1,
      loop: false,
      grid: {
        fill: 'row',
        rows: 1,
      },
      grabCursor: true,
      spaceBetween: 38,

      breakpoints: {
        320: {
          direction: 'horizontal',
          slidesPerView: 2.4,
          slidesPerGroup: 1,
          spaceBetween: 16,
        },

        767: {
          direction: 'vertical',
          slidesPerView: 4,
          spaceBetween: 18,
        },

        1024: {
          direction: 'horizontal',
          slidesPerView: 3.3,
          slidesPerGroup: 1,
        },

        1309: {
          slidesPerView: 4,
        },
      },
    })
  } // thumbs-main

  if (swiperId === 'thumbs-modal') {
    swiper = new Swiper(item, {
      direction: 'horizontal',
      slidesPerView: 3,
      slidesPerGroup: 1,
      loop: false,
      grid: {
        fill: 'row',
        rows: 1,
      },
      grabCursor: true,
      spaceBetween: 38,

      breakpoints: {
        320: {
          slidesPerView: 1,
          spaceBetween: 16,
        },

        767: {
          slidesPerView: 2,
          spaceBetween: 18,
        },

        1024: {
          slidesPerView: 3,
        },

        1309: {
          slidesPerView: 4,
        },
      },
    })

    setControlButtonsSlide(swiperId, item);
  } // thumbs-modal

})

function setControlButtonsSlide(targetName, targetItem) {
  const controlButtons = findControlButtons(targetName);

  controlButtons[0].addEventListener('click', () => {
    console.log();
    targetItem.swiper.slidePrev();
  })

  controlButtons[1].addEventListener('click', () => {
    targetItem.swiper.slideNext();
  })
}

function setControlButtonsJump(targetName, targetItem) {
  const controlButtons = findControlButtons(targetName);

  controlButtons[0].addEventListener('click', () => {
    targetItem.swiper.slideTo(1);
  })

  controlButtons[1].addEventListener('click', () => {
    targetItem.swiper.slideTo(10);
  })

  controlButtons[2].addEventListener('click', () => {
    targetItem.swiper.slideTo(16);
  })



}

function findControlButtons(targetName) {
  return document.querySelectorAll(`[data-swiper-btn-target="${targetName}"]`);
}

