

function createSelectus(titleText) {
  const selectus = createTag('div', ['selectus', 'form__select']);
  const title = createTag('h3', ['selectus__title']);
  const iconWrapper = createTag('span', ['selectus__wrap-icon']);
  const optionsPanelWrapper = createTag('div', ['selectus__panel-wrapper', 'selectus__panel-wrapper--hide']);
  const optionsPanel = createTag('div', ['selectus__panel']);

  selectus.setAttribute('tabindex', '0')
  title.textContent = titleText;

  iconWrapper.innerHTML = iconArrow;
  selectus.append(title);
  selectus.append(iconWrapper);
  selectus.append(optionsPanelWrapper);
  optionsPanelWrapper.append(optionsPanel);

  return {
    selectus,
    title,
    optionsPanelWrapper,
    optionsPanel,
  }
}

function createOption(title, value) {
  const label = createTag('label', ['selectus__label']);
  const input = createTag('input', ['selectus__input']);
  const span = createTag('span', ['selectus__span']);

  span.textContent = title;
  input.setAttribute('type', 'radio');
  input.setAttribute('value', value);

  label.append(span);
  label.append(input);

  return {
    label,
    input,
    span,
  }
}

function createTag(tagName, tagClasses = []) {
  const tag = document.createElement(tagName);

  tagClasses.forEach((tagClass) => {
    tag.classList.add(tagClass);
  });

  return tag;
}

const iconArrow = `<svg width="13" height="13" viewBox="0 -3 13 13" fill="none" xmlns="http://www.w3.org/2000/svg">
<path fill-rule="evenodd" clip-rule="evenodd" d="M6.65674 5.80521L11.5593 0.902607L12.3136 1.65685L6.65674 7.31371L0.999884 1.65685L1.75413 0.902607L6.65674 5.80521Z" fill="#A65CF0"/>
</svg>`


