
const targetElementList = document.querySelectorAll('[data-target="selectus');

targetElementList.forEach((targetElement) => {
  renderSelectus(targetElement)
})


function renderSelectus(targetElement) {
  const titleText = getTitle(targetElement);
  const selectus = createSelectus(titleText);
  const customSelect = selectus.selectus;
  const panelWrapper = selectus.optionsPanelWrapper;

  renderOptionsList(selectus, targetElement);
  hideTargetElement(targetElement);

  targetElement.insertAdjacentElement('afterend', customSelect);

  customSelect.addEventListener('click', (event) => {

    if (!customSelect.classList.contains('selectus--opened') ) {
      openPanelWrapper(selectus)
      openOptionsPanel(customSelect, event);
      onClickCloseOptionsPanel(customSelect);
      setSizingPanelWrapper(selectus);
    } else {
      closeOptionsPanel(customSelect, event);
      hidePanelWrapper(selectus);
    }
  })

  setSizingPanelWrapper(selectus);
  hidePanelWrapper(selectus);
}

function renderOptionsList(selectus, targetElement) {
  const optionsList = targetElement.querySelectorAll('option');

  for (let i = 0; i < optionsList.length; i++ ) {
    let element = optionsList[i];
    if (element.selected) {
      continue
    } else {

      const option = createOption(element.innerText, element.value);

      selectus.optionsPanel.append(option.label);

      option.input.addEventListener('click', (event) => {

        setTitleText(selectus, event);
        closeOptionsPanel(selectus.selectus, event);
        hidePanelWrapper(selectus);
      })
    }
  }
}

function getTitle(targetElement) {
  if (targetElement.querySelector('option[selected]')) {
    return targetElement.querySelector('option[selected]').textContent;
  } else {
    return targetElement.querySelectorAll('option')[0].textContent;
  }
}

function setSizingPanelWrapper(selectus) {
  selectus.optionsPanelWrapper.style.width = selectus.optionsPanel.offsetWidth  + "px";
  selectus.optionsPanelWrapper.style.height = selectus.optionsPanel.offsetHeight  + "px";
}

function onClickCloseOptionsPanel(select) {
  document.addEventListener('click', outsideClickListener);

  function outsideClickListener(event) {
    if (!select.contains(event.target)) {
      closeOptionsPanel(select, event);
      document.removeEventListener('click', outsideClickListener);
    }
  }
}

function openOptionsPanel(select, event) {
  setTimeout(() => {
    select.classList.add('selectus--opened')
  }, 1)
  event.stopPropagation();
}

function closeOptionsPanel(select, event) {
  select.classList.remove('selectus--opened');
  event.stopPropagation();
}

function openPanelWrapper(selectus) {
  selectus.optionsPanelWrapper.classList.remove('selectus__panel-wrapper--hide');
}

function hidePanelWrapper(selectus) {
  setTimeout(() => {
    selectus.optionsPanelWrapper.classList.add('selectus__panel-wrapper--hide');
  }, 400)
}

function setTitleText(selectus, event) {
  selectus.title.textContent = event.target.value;
}

function hideTargetElement(targetElement) {
  targetElement.classList.add('element--invisible');
}
